import React from "react";
import BotonMenu from './BotonMenu';

function Menu(props) {

  function pulsadoBoton(eleccion){
    props.onPulsado(eleccion);
  }

  return (
    <div className="menu">
        <BotonMenu opcion="marketing" clase="marketing" boton="Marketing" pulsadoMenu={pulsadoBoton}/>
        <BotonMenu opcion="web" clase="programacion" boton="Programacion" pulsadoMenu={pulsadoBoton}/>
        <BotonMenu opcion="computer" clase="diseno" boton="Diseño" pulsadoMenu={pulsadoBoton}/>
        <BotonMenu opcion="email" clase="contacto" boton="Empresa" pulsadoMenu={pulsadoBoton}/>    
    </div>
  );
}

export default Menu;