import React, {useState} from 'react';
import Logo from './Logo';
import Menu from './Menu';
import LogoMenu from './LogoMenu';
import Texto from './Texto'
import Footer from './Footer';

import computer from "../svg/computer.svg";
import marketing from "../svg/digital-marketing.svg";
import email from "../svg/email.svg";
import web from "../svg/web.svg";
import empresa from '../svg/desarrollo-web.svg';

function Home(){

    const [textoMenu, setTextoMenu] = useState("menu");

    function cambiarTexto(eleccion){
        setTextoMenu(eleccion);
        console.log(textoMenu);
    }

    function sendMail(){
        window.location = "mailto:agp8119@gmail.com";
    }

    return(
        <div className="home">
            <header className="header">
                <Logo clase="logoHome"/>
                <Menu onPulsado={cambiarTexto}/>
            </header>
            <main className="main">
                <div className="logos">
                    <LogoMenu logo={marketing} onPulsado={cambiarTexto} icono="marketing" />
                    <LogoMenu logo={computer} onPulsado={cambiarTexto} icono="computer" />
                    <LogoMenu logo={web} onPulsado={cambiarTexto} icono="web" />
                    <LogoMenu grid="si" logo={empresa} onPulsado={cambiarTexto} icono="email" />
                    <img className="logoSVG" src={email} alt="email" onClick={sendMail} />  
                </div>
                <Texto contenido={textoMenu}/>
            </main>
            <Footer />
        </div>
    );
}

export default Home;