import React from "react";

function Footer(props) {

  var claseFooter = "footer";

  if(props.menu === false){
    claseFooter = "footer volverMenuMovil";
  }

  function onPulsadoMovil(){
    if(props.activarMenu){
      props.activarMenu();
    }
  }

  return (
    
    <footer className={claseFooter} onClick={onPulsadoMovil}>
      CodeOn&#174; 2021
    </footer>
      
  );
}

export default Footer;