import React from 'react';

import marketing from "../svg/digital-marketing.svg";

function PasoInicio(props){

    function pulsarProceso(event){
        props.Iniciar();
    }

    return(
        <div className="contenedorMarketing">
            <img className="logoSVGPasos" src={marketing} alt="Marketing" />
            <p className="textoHome">
                Para poder diseñar una buena estrategia de marketing tenemos un proceso propio. Es nuestro método para poder brindarte el servicio más personalizado. Y, si nos lo permites, te lo contamos.
            </p>
            <h3 className="proceso" onClick={pulsarProceso}>PROCESO</h3>
        </div>
    );
}

export default PasoInicio;