import React from "react";

function OpcionMovil(props) {

    function pulsadoImagen(){
        props.onPulsado(props.opcion);
    }

  return (
    
      <div className="contenedorOpcionMovil" onClick={pulsadoImagen}>
          <img className="imagenOpcionMovil" src={props.logo} alt={props.icono} />
          <span className="spanOpcionMovil">{props.icono}</span>
      </div>
      
  );
}

export default OpcionMovil;