import React from "react";
import ServiciosProgramacion from './ServiciosProgramacion';

import optimizarWeb from '../svg/mejoramiento.svg';
import movil from '../svg/smartphone.svg';
import chatbot from '../svg/chatbot.svg';

function ContenidoWeb() {

  const textoOptimizacion = "Puede que ya tengas una web pero va demasiado lenta, se descolocan los elementos cuando la visitas desde el móvil o hay partes que han dejado de funcionar y no sabes el motivo. Para estos casos ofrecemos un servicio gratuito de análisis. Donde te informaremos de los tiempos de carga y los motivos que los provocan. De esta manera podemos diseñar soluciones personalizadas para optimizar su funcionamiento. No debemos de perder un cliente potencial por una carga de pantalla demasiado lenta.";
  const textoMoviles = "En caso de que las necesidades de vuestro proyecto necesiten de una aplicación (APP) para móvil tenemos soluciones para Android e IOS. Podemos incluso conectarlas a la web de la empresa si esta existe. Así, por ejemplo, podemos ofrecer desde accesos privados a las bases de datos de la empresa hasta videojuegos para promocionar un evento.";
  const textoChatBot = "Uno de los mayores avances en cuanto a mejorar la atención al cliente es la implementación de tecnologías de Inteligencia Artifical. Gracias a ellas podemos desarrollar un sistema que permitirá atender a los clientes de forma inmediata sin que se den cuenta de que quien les escribe no es una persona. Así recogeremos los datos de una incidencia o resolveremos alguna duda de forma automática.";

  return (
  
    <div className="contenedorProgramacion">
        <h3 className="optimizacion" >Optimización</h3>
        <ServiciosProgramacion Logo={optimizarWeb} Texto={textoOptimizacion} />
        <h3 className="moviles" >Móviles</h3>
        <ServiciosProgramacion Logo={movil} Texto={textoMoviles} />
        <h3 className="chatBot" >Chatbot</h3>
        <ServiciosProgramacion Logo={chatbot} Texto={textoChatBot} />
    </div>
      
  );
}

export default ContenidoWeb;