import React from "react";

function LogoMenu(props) {
    
  function pulsadoImagen(){
    props.onPulsado(props.icono);
  }

  var clase = "logoSVG";
  if(props.grid==="si"){
    clase = "logoSVG areaGrid";
  }
  return (
    
    <img className={clase} src={props.logo} alt="icono" onClick={pulsadoImagen} />
      
  );
}

export default LogoMenu;