import React, {useState} from 'react';
import Proceso from './Proceso';

import videoLlamada from '../svg/videollamada.svg';
import estudioMercado from '../svg/motivacion.svg';
import acuerdo from '../svg/cooperacion.svg';
import refrescar from '../svg/refrescar.svg';
import flecha from '../svg/flecha.svg';

function Pasos(props){

    const [pasoActual, setPasoActual] = useState(1);
    const paso1 = "Para empezar, conocer el proyecto es fundamental pero lo mismo podemos decir de las personas que lo llevan adelante. La base de nuestro proceso es averiguar de primera mano las necesidades y recursos del proyecto. Cuál es el espíritu de vuestro proyecto.";
    const paso2 = "El segundo paso consiste en realizar un estudio de mercado. Debemos centrarnos en vuestro nicho de mercado, competencia y precios. Realizaremos un dossier con todos los datos como la presencia en las redes sociales y el movimiento local del sector.";
    const paso3 = "Finalmente lanzamos una propuesta donde aportaremos la estrategia a seguir junto con las herramientas idóneas para ello. Por ejemplo qué redes sociales interesa usar, desarrollar en paralelo una app móvil, apostar por la fidelización del cliente...";

    function pulsarIzq(event){
        if(pasoActual>1){
            let pasoAnterior = pasoActual;
            pasoAnterior--;
            setPasoActual(pasoAnterior);
        }else{
            props.Iniciar();
        }
    }

    function pulsarDcha(event){
        if(pasoActual<3){
            let pasoAnterior = pasoActual;
            pasoAnterior++;
            setPasoActual(pasoAnterior);
        }else{
            props.Iniciar();
        }
    }

    return(
        <div className="contenedorPasos">
            {pasoActual === 1 ? <Proceso Logo={videoLlamada} Texto={paso1}/> : null }
            {pasoActual === 2 ? <Proceso Logo={estudioMercado} Texto={paso2}/> : null }
            {pasoActual === 3 ? <Proceso Logo={acuerdo} Texto={paso3}/> : null }

            <div className="pasos">

                <div className="contFlecha center">
                    <img className="flecha flechaIzq" src={flecha} alt="Flecha izquierda." onClick={pulsarIzq}/>
                </div>
                
                <div className="numeroPaso center">
                    {pasoActual}
                </div>

                <div className="center altura">
                    <img className="flecha flechaDcha" src={pasoActual < 3 ? flecha : refrescar} alt="Flecha derecha." onClick={pulsarDcha}/>
                </div>
                
            </div>
        </div>
    );
}

export default Pasos;