import React, {useState} from "react";
import Pasos from "./Pasos";
import PasoInicio from './PasoInicio';

function ContenidoMarketing() {

  const [primerPaso, setPrimerPaso] = useState(false);

  function iniciarPasos(){
    setPrimerPaso(true);
  }

  function reiniciarPasos(){
    setPrimerPaso(false);
  }

  return (

    <div>
      {primerPaso ? <Pasos Iniciar={reiniciarPasos}/> : <PasoInicio Iniciar={iniciarPasos}/> }
    </div>  
  );
}

export default ContenidoMarketing;