import React, {useState} from 'react';

import Logo from './Logo';
import MenuMovil from './MenuMovil';
import Footer from './Footer';
import Texto from './Texto';

function Movil(){

    const [menuMovilVisible, setMenuMovilVisible] = useState(true);
    const [opcionActiva, setOpcionActiva] = useState("menu");    

    function recibirOpcion(opcion){
        if(opcion === "contacto"){
            window.location = "mailto:agp8119@gmail.com";
        }else{
            setMenuMovilVisible(false);
            setOpcionActiva(opcion);
        }
        
    }

    function reActivarMenu(){
        setMenuMovilVisible(true);
    }

    return(
        <div className="contenedorMovil">
            <Logo clase="logoMovil"/>
            {menuMovilVisible ? <MenuMovil onPulsado= {recibirOpcion} /> : <Texto contenido={opcionActiva} />}
            <Footer activarMenu={reActivarMenu} menu={menuMovilVisible}/>
        </div>
    );
}

export default Movil;