import React from "react";
import ContenidoMenu from './ContenidoMenu';
import ContenidoWeb from './ContenidoWeb';
import ContenidoEmpresa from './ContenidoEmpresa';
import ContenidoMarketing from './ContenidoMarketing';
import ContenidoDesign from './ContenidoDesign';

function Texto(props) {

  const contenido = props.contenido;

  return (
    
    <div className="texto">
        {contenido === "menu" ? <ContenidoMenu /> : null}
        {contenido === "web" ? <ContenidoWeb /> : null}
        {contenido === "email" ? <ContenidoEmpresa /> : null}
        {contenido === "marketing" ? <ContenidoMarketing /> : null}
        {contenido === "computer" ? <ContenidoDesign /> : null}
    </div>
      
  );
}

export default Texto;