import React from "react";

import OpcionMovil from './OpcionMovil';

import computer from "../svg/computer.svg";
import marketing from "../svg/digital-marketing.svg";
import email from "../svg/email.svg";
import web from "../svg/web.svg";
import empresa from '../svg/desarrollo-web.svg';

function MenuMovil(props) {

    function cambiarTexto(data){
        props.onPulsado(data);
    }

  return (
    
      <div className="contenedorMenuMovil">
          <OpcionMovil opcion="marketing" icono="Marketing" logo={marketing} onPulsado={cambiarTexto}/>
          <OpcionMovil opcion="web" icono="Desarrollo" logo={web} onPulsado={cambiarTexto}/>
          <OpcionMovil opcion="computer" icono="Diseño" logo={computer} onPulsado={cambiarTexto}/>
          <OpcionMovil opcion="email" icono="Empresa" logo={empresa} onPulsado={cambiarTexto}/>
          <OpcionMovil opcion="contacto" icono="Contacto" logo={email} onPulsado={cambiarTexto}/>
      </div>
      
  );
}

export default MenuMovil;